*{
  margin:0;
  
}

body {
  background-color:rgba(234,237,237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
    height: 60px;
    display:flex;
    align-items: center;
    background-color: #131921;
    position:-webkit-sticky;
    position:sticky;
    top:0;
    z-index:100;
 }
 .header__logo{
    width: 100px;
    object-fit:contain;
    margin:0 20px;
    margin-top: 18px;
    }
.header__search{ 
   display: flex;
   flex:  1 1;
   align-items:center;
   border-radius:24px;

 }
.header__searchInput{
   height:12px;
   padding:10px;
   border:none;
   width: 100%;
 }
.header__searchIcon{
   padding: 5px;
   height: 22px !important ;
   background-color: #cd9042;
   

}
.header__optionLineOne{
   font-size:10px;
}
.header__optionLineTwo{
   font-size:13px;
   font-weight:800;
}
.header__optionBasket{
    display:flex;
    align-items:center;
    color:white;
}
.header__basketCount{
       margin-left: 10px;
       margin-right:10px;
}
.header__nav{
   display:flex;
   justify-content:space-evenly;
}
.header__option{
display:flex;
flex-direction: column;
margin-left:10px;
margin-right: 10px;
color: white;
}

.home{
    display:flex;
    justify-content:center;
    margin-left:auto;
    margin-right:auto;
    max-width:1500px

}
.home__row{
    display:flex;
    z-index:1;
    margin-left:5px;
    margin-right:5px;
}
.home__image{
    width: 100%;
    z-index: -1;
    margin-bottom:-150px;
    -webkit-mask-image:linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
            mask-image:linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0)); 
}
.home__container{
       
}
.product{
    display: flex;
    flex-direction:column;
    align-items:center;
    justify-content:flex-end;   
    margin:10px;
    padding:20px;
    width:100%;
    max-height:400px;
    min-width:100px;
    background-color: white;
    z-index:1;

}
.product__rating{
        display:flex;
}
.product > img{
     max-height:200px;
     width:100%;
     object-fit:contain;
     margin-bottom:15px;
}   
.product > button{
    background:#f0c14b;
    border:1px solid;
    margin-top:10px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111;
}
.product__price{
    margin-top:5px; 
    }

.product__info{
    height: 100px;
    margin-bottom: 15px;
}

.checkout{
    display:flex;
    padding:20px;
    background-color:white;
    height:-webkit-max-content;
    height:max-content;
}
.checkout__ad{
    width:100%;
    margin-bottom: 10px;
}
.checkout__title{
    margin-right:10px;
    padding:10px;
    border-bottom:1px solid lightgray;

}
.subtotal{
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    width:300px;
    height:100px;
    padding:20px;
    background-color:#f3f3f3;
    border:1px solid #dddddd;
    border-radius:3px;

}
.subtotal__gift{
    display: flex;
    align-items:center;
}
.subtotal__gift > input{
    margin-right:5px;
} 
.subtotal > button {
    background: #f0c140;
    border-radius: 2px;
    width:100%;
    height:30px;
    border:1px solid;
    margin-top:10px;
    border-color:#a88734 #9c7e31 #846a29 ;
    color:#111;
}
.checkoutProduct{
display:flex;
margin-top:20px;
margin-bottom:20px;
}
.checkoutProduct__info{
    padding-left:20px;


}
.checkoutProduct__info > button{
    background-color:#f0c14b ;
    border:1px solid;
    margin-top: 10px;
    border-color:#a88734 #9c7e31 #846a29;
    color:#111; 
}

.checkoutProduct__image{
    object-fit:contain;
    width:180px;
    height:180px;

}
.checkoutProduct__rating{
    display:flex;

}
.checkoutProduct__title{
     font-size:17px;
     font-weight:800;
}

.login{
        background-color:white;
        height:100vh;
        display:flex;
        flex-direction:column;
        align-items:center;
}

.login__logo{
    margin-top:20px;
    margin-bottom:20px;
    object-fit:contain;
    width:100px;
    margin-right:auto;
    margin-left:auto;

}
.login__container{
    width:300px;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    display:flex;
    flex-direction:column;
    border-radius:5px;
    border:1px solid lightgray;
    padding: 20px;
}
.login__container > h1{
            font-weight:500;
            margin-bottom:20px;
             
}


.login__container > form >h5{
    margin-bottom:5px;


}
.login__container > form > input{
    height:30px;
    margin-bottom:10px;
    background-color: white;
    width: 98%;
}
.login__container > p{
    margin-top:15px;
    font-size:12px;

}
.login__signInButton{
    background:#f0c14b;
    border-radius:2px;
    width:100%;
    height:30px;
    border:1px solid;
    margin-top:10px;
    border-color: #a88734 #9c7e31 #846a29;

}
.login__registerButton{
        border-radius:2px;
        width:100%;
        height:30px;
        border:1px solid;
        margin-top:10px ;
        border-color:darkgray; 

}
.payment{
    background-color:white;
}
.payment__container>h1{
    text-align:center;
    padding:10px;
    font-weight:400;
    background-color:rgb(234,237,237);
    border-bottom:1px solid lightgray;

}
.payment__container>h1 a{
   text-decoration:none;

}
.payment__section{
    display:flex;
    padding:20px;
    margin:0 20px;
    border-bottom:1px  solid lightgray;
}
.payment__title{
    flex:0.2 1;

}
.payment__address, .payment__items, .payment__details{
     flex:0.8 1; 
}

